@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;400;500;700;800&display=swap);
html{scroll-behavior:smooth}body{background-color:#232323;margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:white;scrollbar-width:none}body::-webkit-scrollbar{width:10px;overflow:overlay}@media (max-width: 768px){body::-webkit-scrollbar{display:none}}body::-webkit-scrollbar-track{overflow:overlay;background-color:#e4e4e400;border-radius:100px}body::-webkit-scrollbar-thumb{overflow:overlay;background-color:#000000;border-radius:100px}.subheading{color:#F26B4E;font-weight:800;text-align:center;font-size:40px}@media (max-width: 768px){.subheading{font-size:30px}}.SectionDiv{margin-top:150px;height:100vh;display:flex;align-items:center;flex-direction:column;justify-content:center;scroll-snap-align:start}.fade-in{opacity:1;-webkit-animation-name:fadeInOpacity;animation-name:fadeInOpacity;-webkit-animation-iteration-count:1;animation-iteration-count:1;-webkit-animation-timing-function:ease;animation-timing-function:ease;-webkit-animation-duration:.3s;animation-duration:.3s}@-webkit-keyframes fadeInOpacity{0%{opacity:0}100%{opacity:1}}@keyframes fadeInOpacity{0%{opacity:0}100%{opacity:1}}

.testDiv{display:flex;flex-direction:column;height:100vh;justify-content:center;align-items:center}.textDivInside{display:flex;justify-content:center;align-items:center;height:80px}.letterDiv{height:100%;padding:0;padding-left:10px;display:flex;flex-direction:row;align-items:center}.letterDiv img{border:0}.letterLong{background-color:white;height:80px;width:20px}.letterWide{background-color:white;height:20px;width:30px}.TitleDiv{padding-left:100px;padding-top:150px;padding-bottom:150px}@media (max-width: 768px){.TitleDiv{padding-left:25px;padding-top:250px}}.title1{color:#888888;font-size:100px;font-weight:800;margin:0;line-height:60%}@media (max-width: 768px){.title1{font-size:80px}}.title2{color:white;font-size:120px;font-weight:800;margin:0;line-height:80%;margin-top:20px}@media (max-width: 768px){.title2{font-size:80px}}.title3{color:#888888;font-size:50px;font-weight:200;margin:0;line-height:80%;margin-top:20px}@media (max-width: 768px){.title3{font-size:30px}}

.NavBar{height:50px;width:40%;padding:0 30%;position:fixed;background-color:#232323;z-index:2}@media (max-width: 768px){.NavBar{padding:0 0%;width:100%}}.NavBarList{height:100%;display:flex;flex-direction:row;justify-content:space-around;align-items:center}@media (max-width: 768px){.NavBarList{justify-content:space-around;padding:0;width:90%}}.NavBarList a{color:white;width:5%;font-weight:500;font-size:small;text-decoration:none}.NavBarListItem:hover{color:#F26B4E}

.desc{margin:40px 20%;text-align:center}

.PortfolioDiv{padding-top:50px}

.item{display:flex;align-items:center;justify-content:center;color:white;position:relative;transition:all .5s ease;background-color:black}.ImgItem{width:100%;height:100%;object-fit:cover;z-index:1}img{transition:all .5s ease}.newImg:hover{opacity:0%;z-index:0}.ImgTitle{position:absolute;pointer-events:none}@media (max-width: 768px){.ImgTitle{font-size:small}}.newImg{width:100%;height:100%}.grid{margin:40px 0;background-color:black}.grid2{background-color:black;display:grid;grid-template-columns:repeat(5, 1fr)}@media (max-width: 768px){.grid2{grid-template-columns:repeat(3, 1fr)}}.one{display:flex;align-items:center;justify-content:center;color:white;position:relative;transition:all .5s ease;background-color:black}.one-tall{grid-row:span 2 / auto;grid-column:span 2 / auto}@media (max-width: 768px){.one-tall{grid-row:span 1 / auto;grid-column:span 1 / auto}}

.ModalBackground{width:100%;height:100%;background:rgba(0,0,0,0.829);position:fixed;display:flex;justify-content:center;align-items:center;z-index:2;top:0%;left:0%}.ModalWrapper{width:40%;box-shadow:0 5px 16px rgba(0,0,0,0.2);background:#704141;color:#000;display:flex;position:relative;z-index:10;border-radius:10px}@media (max-width: 768px){.ModalWrapper{width:80%}}.ModalImg{width:100%}

.ContactInnerDiv a{margin:0 10px}.hoverEffect{transition:.3s}.hoverEffect:hover{color:#F26B4E}

